import React from 'react';

import clsx from 'clsx';
import { useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';

import { makeStyles } from '@material-ui/core/styles';

import { scrollDownAnimatedArrowStyleLarge as stylesLarge } from 'assets/jss/material-kit-react/components/scrollDownAnimatedArrowStyle.js';
import { scrollDownAnimatedArrowStyleSmall as stylesSmall } from 'assets/jss/material-kit-react/components/scrollDownAnimatedArrowStyle.js';

const useLargeStyles = makeStyles(stylesLarge);
const useSmallStyles = makeStyles(stylesSmall);

export default function ScrollDownAnimatedArrow() {
  const theme = useTheme();
  const isLargeScreen = useMediaQuery(theme.breakpoints.up('sm'));
  const classesLarge = useLargeStyles();
  const classesSmall = useSmallStyles();
  return (
    <div
      className={clsx(
        isLargeScreen && classesLarge.container,
        !isLargeScreen && classesSmall.container,
      )}
    >
      <div
        className={clsx(
          isLargeScreen && classesLarge.chevron,
          !isLargeScreen && classesSmall.chevron,
        )}
      ></div>
      <div
        className={clsx(
          isLargeScreen && classesLarge.chevron,
          !isLargeScreen && classesSmall.chevron,
        )}
      ></div>
      <div
        className={clsx(
          isLargeScreen && classesLarge.chevron,
          !isLargeScreen && classesSmall.chevron,
        )}
      ></div>
    </div>
  );
}
