const BASE_LARGE = '0.6rem';
const BASE_SMALL = '0.3rem';

const getStyle = (base) => ({
  container: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  chevron: {
    position: 'absolute',
    zIndex: 99999,
    width: `calc(${base} * 3.5)`,
    height: `calc(${base} * 0.8)`,
    opacity: 0,
    transform: 'scale(0.3)',
    animation: '$move-chevron-large 3s ease-out infinite',
    '&:first-child': {
      animation: '$move-chevron-large 3s ease-out 1s infinite',
    },
    '&:nth-child(2)': {
      animation: '$move-chevron-large 3s ease-out 2s infinite',
    },
    '&:before, &:after': {
      content: "''",
      position: 'absolute',
      zIndex: 99999,
      top: 0,
      height: '100%',
      width: '50%',
      background: '#eeeeee',
    },
    '&:before': {
      left: 0,
      transform: 'skewY(30deg)',
    },
    '&:after': {
      right: 0,
      width: '50%',
      transform: 'skewY(-30deg)',
    },
  },
  '@keyframes move-chevron-large': {
    '25%': {
      opacity: 1,
    },
    '33.3%': {
      opacity: 1,
      transform: `translateY(calc(${base} * 3.8))`,
    },
    '66.6%': {
      opacity: 1,
      transform: `translateY(calc(${base} * 5.2))`,
    },
    '100%': {
      opacity: 0,
      transform: `translateY(calc(${base} * 8)) scale(0.5)`,
    },
  },
});

export const scrollDownAnimatedArrowStyleLarge = getStyle(BASE_LARGE);
export const scrollDownAnimatedArrowStyleSmall = getStyle(BASE_SMALL);
